import { gql, useQuery } from "@apollo/client";
import { Component } from "react";

const POST_QUERY = gql`
	query AllPosts {
		allPosts {
			author {
				email
			}
			content
			title
		}
	}
`;

const POST_SUBSCRIPTION = gql`
  subscription postCreated {
    postCreated {
			author {
				email
			}
			content
			title
    }
  }
`;

export class CommentsPage extends Component {
  componentDidMount() {
		// @ts-ignore
    this.props.subscribeToNewComments();
  }

	render() {
		console.log(this.props);
		
		return (
			<div>
				Test
			</div>
		)
	}
}

function CommentsPageWithData({ params }: any) {
	const { subscribeToMore, ...result } = useQuery(POST_QUERY, { variables: {} });

	return (
		<CommentsPage
			{...result}
			// @ts-ignore
			subscribeToNewComments={() =>
				subscribeToMore({
					document: POST_SUBSCRIPTION,
					// variables: {},
					updateQuery: (prev, { subscriptionData }) => {
						console.log({ prev: prev.allPosts });
						if (!subscriptionData.data) return prev;
						const newFeedItem = subscriptionData.data.postCreated;
						console.log({ subscriptionData });
						
						return Object.assign({}, prev, { feed: [...prev.allPosts, newFeedItem] });
					}
				})
			}
		/>
	);
}

export default CommentsPageWithData;
