import {
  ImageList,
  ImageListItem,
  ImageListItemBar,
  IconButton,
  isWidthUp,
  makeStyles,
  withWidth,
  Typography,
  Chip,
  Tooltip,
} from "@material-ui/core";
import InfoIcon from "@material-ui/icons/Info";
import React, { useEffect, useState } from "react";
import { useIntersectionObserver } from "react-intersection-observer-hook";
import { useHistory, useParams } from "react-router-dom";
import FilterSection from "../../components/FilterSection/FilterSection";
import Loader from "../../components/Loader/Loader";
import Observer from "../../components/Observer/Observer";
import TopNavBarLinks from "../../components/TopNavBarLinks/TopNavBarLinks";
import {
  bearerToken,
  clientId,
  streamsForSpecificGame,
  streamsUrl,
} from "../../config/config";
import { dataCountList } from "../../data/dataCountList";
import { CustomSelectProps } from "../../models/CustomSelectProps";
import { FilterSectionProps } from "../../models/FilterSectionProps";
import { checkIfAllToFetch, checkLanguage } from "../../utilities/Utility";
import VisibilityIcon from "@material-ui/icons/Visibility";
import "./StreamersForGame.scss";
import { StreamerService } from "../GetTopStreamers/GetTopStreamers";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
  },
  gridList: {
    width: "auto",
    height: "auto",
  },
  title: {
    margin: "1rem",
  },
  icon: {
    color: "rgba(255, 255, 255, 0.54)",
  },
  ImageListItem: {
    "&:hover": {
      cursor: "pointer",
    },
  },
  center: {
    display: "flex",
    justifyContent: "center",
  },
}));

const StreamersForGame: React.FC<any> = (props) => {
  const history = useHistory();
  const classes = useStyles();
  const [
    listOfTopStreamersForSpecificGame,
    setListOfTopStreamersForSpecificGame,
  ] = useState<Array<any>>([]);
  const { game_name, game_id } = useParams<any>();
  const [pagination, setPagination] = useState<string>();
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [ref, { entry }] = useIntersectionObserver();
  const [dataCount, setDataCount] = useState<any>(dataCountList[0]);
  const defaultUrlApi = `${streamsUrl}?first=${dataCount.value}`;
  const isVisible = entry && entry.isIntersecting;

  const headers = [
    { label: "Id du stream", key: "id" },
    { label: "Titre", key: "title" },
    { label: "Id de l'utilisateur", key: "user_id" },
    { label: "Pseudo", key: "user_name" },
    { label: "Nombre de spectateurs", key: "stream_viewer_count" },
    { label: "Url de l'aperu du stream", key: "stream_thumbnail_url" },
  ];

  const csvReport = {
    data: listOfTopStreamersForSpecificGame,
    headers: headers,
    filename: `${sessionStorage.getItem(
      "language"
    )} Streamers for ${game_name} - ${new Date()}.csv`,
  };

  const customSelectProps: CustomSelectProps = {
    dataCount: dataCount,
    setDataCount: setDataCount,
  };

  const filterSectionProps: FilterSectionProps = {
    customSelectProps: customSelectProps,
    csvReport: csvReport,
    isLoading: isLoading,
  };

  const booclefetchStreamersUrl = async () => {
    await setIsLoading(true);
    new StreamerService((data: any) => {
      const dataListOfTopStreamersForSpecificGame = [];
        if (!checkIfAllToFetch(dataCount)) {
          setPagination(data.pagination.cursor);
        }
        const list = checkIfAllToFetch(dataCount) ? data : data.data;
        for (const listKey in list) {
          dataListOfTopStreamersForSpecificGame.push({
            id: list[listKey].id,
            user_id: list[listKey].user_id,
            user_name: list[listKey].user_name,
            title: list[listKey].title,
            stream_thumbnail_url: checkIfAllToFetch(dataCount)
              ? list[listKey].thumbnail_url
              : list[listKey].thumbnail_url
                  .replace("{width}", "440")
                  .replace("{height}", "248"),
            stream_viewer_count: list[listKey].viewer_count,
          });
        }
        const result = [
          ...listOfTopStreamersForSpecificGame,
          ...dataListOfTopStreamersForSpecificGame,
        ];
        setListOfTopStreamersForSpecificGame(result);
      setIsLoading(false);
    }, game_id);
  }

  const fetchTopStreamersForSpecificGame = (pagination: any) => {
    let fetchLink = checkLanguage()
      ? `${defaultUrlApi}&game_id=${game_id}&language=${sessionStorage.getItem(
          "language"
        )}`
      : `${defaultUrlApi}&game_id=${game_id}`;

    if (pagination) {
      fetchLink = checkLanguage()
        ? `${defaultUrlApi}&game_id=${game_id}&after=${pagination}&language=${sessionStorage.getItem(
            "language"
          )}`
        : `${defaultUrlApi}&game_id=${game_id}&after=${pagination}`;
    }

    if (checkIfAllToFetch(dataCount)) {
      // fetchLink = `${streamsForSpecificGame}/${game_id}/${sessionStorage.getItem(
      //   "language"
      // )}/descendant`;
      booclefetchStreamersUrl();
      return;
    }

    console.log(fetchLink);

    fetch(fetchLink, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    })
      .then(function (response) {
        return response.json();
      })
      .then((data) => {
        const dataListOfTopStreamersForSpecificGame = [];
        if (!checkIfAllToFetch(dataCount)) {
          setPagination(data.pagination.cursor);
        }
        const list = checkIfAllToFetch(dataCount) ? data : data.data;
        for (const listKey in list) {
          dataListOfTopStreamersForSpecificGame.push({
            id: list[listKey].id,
            user_id: list[listKey].user_id,
            user_name: list[listKey].user_name,
            title: list[listKey].title,
            stream_thumbnail_url: checkIfAllToFetch(dataCount)
              ? list[listKey].thumbnail_url
              : list[listKey].thumbnail_url
                  .replace("{width}", "440")
                  .replace("{height}", "248"),
            stream_viewer_count: list[listKey].viewer_count,
          });
        }
        const result = [
          ...listOfTopStreamersForSpecificGame,
          ...dataListOfTopStreamersForSpecificGame,
        ];
        setListOfTopStreamersForSpecificGame(result);
      });
  };

  const goToStream = (topStreamerForSpecificGame: any) => {
    history.push(
      `/streamer/${topStreamerForSpecificGame.user_name}/${topStreamerForSpecificGame.user_id}`
    );
  };

  const renderViewers = (topStreamerForSpecificGame: any) => {
    return (
      <>
        <div className="viewers-container">
          <VisibilityIcon />
          <Typography>
            {topStreamerForSpecificGame.stream_viewer_count}
          </Typography>
        </div>
      </>
    );
  };

  const renderChipViewers = (topStreamerForSpecificGame: any) => {
    return (
      <>
        <Chip label={renderViewers(topStreamerForSpecificGame)} size="medium" />
      </>
    );
  };

  const renderTopStreamersForSpecificGame = () => {
    if (isLoading) {
      return <Loader />;
    }
    return (
      <ImageList
        rowHeight={"auto"}
        cols={getGridListCols()}
        className={classes.gridList}
      >
        {listOfTopStreamersForSpecificGame.map(
          (topStreamerForSpecificGame: any, idx: number) => {
            return (
              <ImageListItem
                onClick={() => goToStream(topStreamerForSpecificGame)}
                key={idx}
                className={classes.ImageListItem}
              >
                <img
                  src={`${topStreamerForSpecificGame.stream_thumbnail_url}`}
                  alt={topStreamerForSpecificGame.user_name}
                />
                <ImageListItemBar
                  title={`${topStreamerForSpecificGame.user_name}`}
                  subtitle={renderChipViewers(topStreamerForSpecificGame)}
                  actionIcon={
                    <Tooltip
                      title={topStreamerForSpecificGame.title}
                      placement="top"
                    >
                      <IconButton className={classes.icon}>
                        <InfoIcon />
                      </IconButton>
                    </Tooltip>
                  }
                />
              </ImageListItem>
            );
          }
        )}
      </ImageList>
    );
  };

  const intersected = () => {
    fetchTopStreamersForSpecificGame(pagination);
  };

  useEffect(() => {
    if (listOfTopStreamersForSpecificGame.length === 0) {
      fetchTopStreamersForSpecificGame(null);
    }
    if (listOfTopStreamersForSpecificGame.length > 0) {
      setIsLoading(false);
    }
  }, [
    listOfTopStreamersForSpecificGame.length,
    fetchTopStreamersForSpecificGame,
  ]);

  useEffect(() => {
    intersected();
  }, [isVisible]);

  useEffect(() => {
    setListOfTopStreamersForSpecificGame([]);
    fetchTopStreamersForSpecificGame(null);
  }, [dataCount]);

  const getGridListCols = () => {
    if (isWidthUp("xl", props.width)) {
      return 6;
    }

    if (isWidthUp("lg", props.width)) {
      return 5;
    }

    if (isWidthUp("md", props.width)) {
      return 4;
    }

    if (isWidthUp("sm", props.width)) {
      return 2;
    }

    if (isWidthUp("xs", props.width)) {
      return 1;
    }

    return 1;
  };

  const renderObserver = () => {
    if (!checkIfAllToFetch(dataCount)) {
      return <Observer ref={ref}></Observer>;
    }
  };

  return (
    <div id="streamersForGameContainer">
      <TopNavBarLinks></TopNavBarLinks>
      <h2 className={classes.title}>Streamers pour '{game_name}'</h2>
      <FilterSection {...filterSectionProps} />
      <div className={classes.root}>{renderTopStreamersForSpecificGame()}</div>
      {renderObserver()}
    </div>
  );
};

export default withWidth()(StreamersForGame);
