import React from "react";
import { Button, Chip, makeStyles } from "@material-ui/core";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import VisibilityIcon from "@material-ui/icons/Visibility";
import FastForwardIcon from "@material-ui/icons/FastForward";

import TopNavBarLinks from "../../components/TopNavBarLinks/TopNavBarLinks";
import Select from "../../components/CustomSelect";
import { timeViewers } from "../../data/dataCountList";
import Loader from "../../components/Loader/Loader";

import "./Streamer.scss";
import { useStreamer } from "./useStreamer";

const useStyles = makeStyles((theme) => ({
  title: {
    margin: "1rem",
  },
  chip: {
    height: "50px",
    padding: "1rem",
    fontSize: "22px",
  },
  twitchEmbed: {
    margin: "25px 0px",
  },
}));

const Streamer: React.FC = () => {
  const {
    loading,
    streamer_user_name,
    job,
    item,
    setJob,
    followers,
    streamViewerCount,
    setItem,
    streamer,
    goToProfile,
    goToClips,
    getAllViewer,
    updateCron,
    addCron,
    getVisibility,
    loading_query,
    setLoadingQuery,
  } = useStreamer();
  const classes = useStyles();

  return (
    <div id="streamer">
      <TopNavBarLinks></TopNavBarLinks>
      <h2 className={classes.title}>{streamer?.streamTitle}</h2>
      <Button variant="contained" color="primary" onClick={() => goToProfile()}>
        Profil du streamer
      </Button>
      <Button variant="contained" color="primary" onClick={() => goToClips()}>
        Captures du streamer
      </Button>
      <Select setItem={setItem} item={item} options={timeViewers} />
      {loading_query ? <Loader /> : <Button variant="contained" color="primary" onClick={async () => {
        if (job?.id) {
          await setLoadingQuery(true);
          await updateCron({variables: { updateCronData: {id: job?.id, status: 0} }});
          await getAllViewer();
          await setJob({})
          await setLoadingQuery(false);
        } else {
          await setLoadingQuery(true);
          await addCron({ variables: { addCronData: {
            "status": 1,
            "time": item?.value,
            "user": "2",
            "streamer": streamer_user_name
          } }})
          await setLoadingQuery(false);
        }
      }}>
       {`${ job?.id ? 'Stop' :'Start'}`}
      </Button>}
      <div id="twitch-embed" className={classes.twitchEmbed}></div>
      <div className="streamerInfoContainer">
        <Chip
          icon={<AccountCircleIcon />}
          label={streamer_user_name}
          clickable
          color="primary"
          size="medium"
        />
       {loading ? <Loader /> : <div onClick={async () => await getVisibility()}>
          <Chip
            icon={<VisibilityIcon />}
            label={streamViewerCount}
            clickable
            color="primary"
            size="medium"
          />
        </div>}
        <Chip
          icon={<FastForwardIcon />}
          label={`Followers: ${followers}`}
          clickable
          color="primary"
          size="medium"
        />
      </div>
    </div>
  );
};

export default Streamer;
