import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import "./App.scss";
import Clip from "./views/GetClips/Clip";
import GetClips from "./views/GetClips/GetClips";
import GetTopGames from "./views/GetTopGames/GetTopGames";
import GetTopStreamers from "./views/GetTopStreamers/GetTopStreamers";
import Streamer from "./views/Streamer/Streamer";
import StreamerProfile from "./views/Streamer/StreamerProfile";
import StreamersForGame from "./views/StreamersForGame/StreamersForGame";
import GraphQLTest from "./views/GraphQL";
import Video from "./views/Video/Video";
import { connect } from "react-redux";
import mapStateToProps from "./services/redux/mapStateToProps";
import mapDispatchToProps from "./services/redux/mapDispatchToProps";
// import AuthHome from "./views/AuthHome";

function App(props: any) {
  // console.log({auth: props.auth});
  // if(!props?.auth?.isAuth) {
  //   return <AuthHome />
  // }

  const renderSwitch = (rt: Array<any>) => {
    const res = rt.map((route: any, idx: number) => (
      <Route exact path={route.path} component={route.component} key={idx} />
    ));
    return res;
  };

  const defaultRoutes = [
    {
      path: "/",
      component: GetTopGames,
    },
    {
      path: "/test-graphql",
      component: GraphQLTest,
    },
    {
      path: "/streamers",
      component: GetTopStreamers,
    },
    {
      path: "/streamers-par-jeu/:game_name/:game_id",
      component: StreamersForGame,
    },
    {
      path: "/streamer/:streamer_user_name/:streamer_id",
      component: Streamer,
    },
    {
      path: "/profil-streamer/:streamer_user_name/:streamer_id",
      component: StreamerProfile,
    },
    {
      path: "/captures/:streamer_name/:streamer_id",
      component: GetClips,
    },
    {
      path: "/capture/:clip_id",
      component: Clip,
    },
    {
      path: "/video/:video_id",
      component: Video,
    },
  ];

  return (
    <div className="App">
      <Router>
        <div>
          <Switch>{renderSwitch(defaultRoutes)}</Switch>
        </div>
      </Router>
    </div>
  );
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(App);

