import { Button, Chip, Grid, makeStyles } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import TopNavBarLinks from "../../components/TopNavBarLinks/TopNavBarLinks";
import { bearerToken, clientId, clipsUrl } from "../../config/config";
import { TwitchClip } from "react-twitch-embed";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import VisibilityIcon from "@material-ui/icons/Visibility";

const useStyles = makeStyles((theme) => ({
  title: {
    margin: "1rem",
  },
  chip: {
    height: "50px",
    padding: "1rem",
    fontSize: "22px",
  },
  twitchEmbed: {
    margin: "25px 0px",
  },
}));

const Clip: React.FC = () => {
  const classes = useStyles();
  const [clip, setClip] = useState<any>();
  const { clip_id } = useParams<any>();
  const history = useHistory();

  const fetchClip = () => {
    const fetchLink = `${clipsUrl}?id=${clip_id}`;

    fetch(fetchLink, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    })
      .then(function (response) {
        return response.json();
      })
      .then((data) => {
        setClip(data.data[0]);
      });
  };

  const goToProfile = () => {
    history.push(
      `/profil-streamer/${clip?.broadcaster_name}/${clip?.broadcaster_id}`
    );
  };
  const goToVideo = () => {
    history.push(`/video/${clip?.video_id}`);
  };

  const renderButtonGoToVideo = () => {
    if (clip?.video_id) {
      return (
        <Button variant="contained" color="primary" onClick={() => goToVideo()}>
          Vido complte
        </Button>
      );
    }
  };

  useEffect(() => {
    fetchClip();
  }, []);

  return (
    <div id="streamer">
      <TopNavBarLinks></TopNavBarLinks>
      <h2 className={classes.title}>Capture de {clip?.broadcaster_name}</h2>
      <h3>{clip?.title}</h3>
      <br />
      <Button variant="contained" color="primary" onClick={() => goToProfile()}>
        Profil du streamer
      </Button>
      {renderButtonGoToVideo()}
      <br />
      <br />
      <Grid item>
        <TwitchClip
          height="500"
          width="100%"
          clip={clip_id}
          parent={["bo.thoussein.fr"]}
        />
      </Grid>
      <Grid className="streamerInfoContainer" item>
        <Chip
          icon={<AccountCircleIcon />}
          label={clip?.broadcaster_name}
          clickable
          color="primary"
          size="medium"
        />
        <Chip
          icon={<VisibilityIcon />}
          label={clip?.view_count}
          clickable
          color="primary"
          size="medium"
        />
      </Grid>
      <br />
    </div>
  );
};

export default Clip;
