import { Button, Chip, Grid, makeStyles } from "@material-ui/core";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import VisibilityIcon from "@material-ui/icons/Visibility";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import { TwitchPlayer } from "react-twitch-embed";
import TopNavBarLinks from "../../components/TopNavBarLinks/TopNavBarLinks";
import { bearerToken, clientId, videosUrl } from "../../config/config";

const useStyles = makeStyles((theme) => ({
  title: {
    margin: "1rem",
  },
  chip: {
    height: "50px",
    padding: "1rem",
    fontSize: "22px",
  },
  twitchEmbed: {
    margin: "25px 0px",
  },
}));

const Video: React.FC = () => {
  const classes = useStyles();
  const [video, setVideo] = useState<any>();
  const { video_id } = useParams<any>();
  const history = useHistory();

  const fetchClip = () => {
    const fetchLink = `${videosUrl}?id=${video_id}`;

    fetch(fetchLink, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    })
      .then(function (response) {
        return response.json();
      })
      .then((data) => {
        setVideo(data.data[0]);
      });
  };

  const goToProfile = () => {
    history.push(`/profil-streamer/${video?.user_name}/${video?.user_id}`);
  };

  const goToClips = () => {
    history.push(`/captures/${video?.user_name}/${video?.user_id}`);
  };

  const renderVideo = () => {
    if (video) {
      return <TwitchPlayer height="500px" width="100%" video={video_id} />;
    }
  };

  useEffect(() => {
    fetchClip();
  }, []);

  return (
    <div id="streamer">
      <TopNavBarLinks></TopNavBarLinks>
      <h2 className={classes.title}>Vido complte de {video?.user_name}</h2>
      <h3>{video?.title}</h3>
      <br />
      <Button variant="contained" color="primary" onClick={() => goToProfile()}>
        Profil du streamer
      </Button>
      <Button variant="contained" color="primary" onClick={() => goToClips()}>
        Captures du streamer
      </Button>
      <br />
      <br />
      {renderVideo()}
      <Grid className="streamerInfoContainer" item>
        <Chip
          icon={<AccountCircleIcon />}
          label={video?.user_name}
          clickable
          color="primary"
          size="medium"
        />
        <Chip
          icon={<VisibilityIcon />}
          label={video?.view_count}
          clickable
          color="primary"
          size="medium"
        />
      </Grid>
      <br />
    </div>
  );
};

export default Video;
