import { Button, Grid } from "@material-ui/core";
import Avatar from "@material-ui/core/Avatar";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemAvatar from "@material-ui/core/ListItemAvatar";
import ListItemText from "@material-ui/core/ListItemText";
import { makeStyles } from "@material-ui/core/styles";
import BrokenImageIcon from "@material-ui/icons/BrokenImage";
import ContactsIcon from "@material-ui/icons/Contacts";
import DescriptionIcon from "@material-ui/icons/Description";
import SubscriptionsIcon from "@material-ui/icons/Subscriptions";
import VisibilityIcon from "@material-ui/icons/Visibility";
import React, { useEffect, useState } from "react";
import NumberFormat from "react-number-format";
import { useHistory, useParams } from "react-router-dom";
import TopNavBarLinks from "../../components/TopNavBarLinks/TopNavBarLinks";
import {
  bearerToken,
  clientId,
  followsUrl,
  usersUrl,
} from "../../config/config";
import ViewStreamIcon from "@material-ui/icons/ViewStream";
import "./StreamerProfile.scss";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
  list: {
    width: "100%",
  },
  title: {
    margin: "1rem",
  },
  table: {
    minWidth: 650,
  },
  large: {
    width: "164px",
    height: "164px",
    margin: "0 auto",
  },
  coverImage: {
    width: "100%",
    margin: "0 auto",
  },
}));

const StreamerProfile: React.FC = () => {
  const classes = useStyles();
  //Params
  const { streamer_user_name, streamer_id } = useParams<any>();
  const [followers, setFollowers] = useState<number>();
  const [user, setUser] = useState<any>();
  const classesRoot = `${classes.list} profile-root`;
  const history = useHistory();

  const fetchFollowers = () => {
    fetch(`${followsUrl}?to_id=${streamer_id}`, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    })
      .then(function (response) {
        return response.json();
      })
      .then((data) => {
        setFollowers(data?.total);
      });
  };

  const fetchUserInformation = () => {
    fetch(`${usersUrl}?id=${streamer_id}`, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    })
      .then(function (response) {
        return response.json();
      })
      .then((data) => {
        setUser(data?.data[0]);
      });
  };

  const goToStreamer = () => {
    history.push(`/streamer/${streamer_user_name}/${streamer_id}`);
  };

  useEffect(() => {
    fetchFollowers();
    fetchUserInformation();
  }, []);

  return (
    <>
      <TopNavBarLinks></TopNavBarLinks>
      <div className={classes.root}>
        <h2 className={classes.title}>Profil de {streamer_user_name}</h2>
        <Grid container>
          <Grid item xs={4}>
            <List className={classesRoot}>
              <ListItem>
                <Avatar
                  alt={user?.display_name}
                  src={user?.profile_image_url}
                  className={classes.large}
                />
              </ListItem>
              <ListItem>
                <img
                  alt={user?.display_name}
                  src={user?.offline_image_url}
                  className={classes.coverImage}
                />
              </ListItem>
              <ListItem>
                <ListItemAvatar>
                  <Avatar>
                    <ContactsIcon />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText primary="Nom" secondary={streamer_user_name} />
              </ListItem>
              <ListItem>
                <ListItemAvatar>
                  <Avatar>
                    <SubscriptionsIcon />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText
                  primary="Followers"
                  secondary={
                    <NumberFormat
                      value={followers}
                      displayType={"text"}
                      thousandSeparator={true}
                      renderText={(value) => <div>{value}</div>}
                    />
                  }
                />
              </ListItem>
            </List>
          </Grid>
          <Grid item xs={8}>
            <List className={classesRoot}>
              <ListItem>
                <ListItemAvatar>
                  <Avatar>
                    <ContactsIcon />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText primary="Nom" secondary={streamer_user_name} />
              </ListItem>
              <ListItem>
                <ListItemAvatar>
                  <Avatar>
                    <BrokenImageIcon />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText
                  primary="Type de diffusion"
                  secondary={user?.broadcaster_type}
                />
              </ListItem>
              <ListItem>
                <ListItemAvatar>
                  <Avatar>
                    <DescriptionIcon />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText
                  primary="Description"
                  secondary={user?.description}
                />
              </ListItem>
              <ListItem>
                <ListItemAvatar>
                  <Avatar>
                    <VisibilityIcon />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText
                  primary="Spectateurs de la chane"
                  secondary={
                    <NumberFormat
                      value={user?.view_count}
                      displayType={"text"}
                      thousandSeparator={true}
                      renderText={(value) => <div>{value}</div>}
                    />
                  }
                />
              </ListItem>
            </List>
            <Button
              variant="contained"
              color="primary"
              onClick={() => goToStreamer()}
            >
              <ViewStreamIcon />
              Live stream
            </Button>
          </Grid>
        </Grid>
      </div>
    </>
  );
};

export default StreamerProfile;
