import { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import { useQuery, useLazyQuery, useMutation } from "@apollo/client";
import { TwitchEmbedLayout, TwitchPlayerEvent, TwitchEmbed } from 'twitch-player';

import { exportToCsv } from "../../services/technique/utils";
import { get } from "../../services/technique/api";

import {
  bearerToken,
  channelsUrl,
  clientId,
  followsUrl,
  streamsUrl,
} from "../../config/config";
import { CRON_ADD, CRON_QUERY, CRON_UPDATE, VIEWER_QUERY } from "./gql";
import { timeViewers } from "../../data/dataCountList";

export const useStreamer = () => {
  const [job, setJob] = useState<any>({});
  const { ...result } = useQuery(CRON_QUERY, { variables: {"skip": 0, "take": 1000, "filter": 1 } });
  const [streamer, setStreamer] = useState<any>();
  const { streamer_user_name, streamer_id } = useParams<any>();
  const [item, setItem] = useState<any>(timeViewers[3]);
  const [streamViewerCount, setStreamViewerCount] = useState<string>();
  const [followers, setFollowers] = useState<any>();
  const [loading, setLoading] = useState<any>(false);
  const [loading_query, setLoadingQuery] = useState<any>(false);
  const history = useHistory();
  const [updateCron, { ...update }] = useMutation(CRON_UPDATE, {
    refetchQueries: [
      {query: CRON_QUERY, variables: {"skip": 0, "take": 1000, "filter": 1 }}, // DocumentNode object parsed with gql
      'GetAllCron' // Query name
    ],
  });
  const [addCron, { ...add }] = useMutation(CRON_ADD, {
    refetchQueries: [
      {query: CRON_QUERY, variables: {"skip": 0, "take": 1000, "filter": 1 }}, // DocumentNode object parsed with gql
      'GetAllCron' // Query name
    ],
    onCompleted(data) {
      console.log({ data });
    },
  });

  const [getAllViewer, { data }] = useLazyQuery(VIEWER_QUERY, {
    fetchPolicy: 'no-cache',
    variables: {"skip": 1, "take": 10, filter: job?.id },
    onCompleted(data) {
      console.log({ data });
      if (Array.isArray(data?.getAllViewer) && data?.getAllViewer?.length > 0) {
        const dataViewer = data?.getAllViewer;
        const arrayType = dataViewer[0].viewers.map((_item: any[], i: number) => {
          let all: any = [];
          for (let index = 0; index < dataViewer.length; index++) {
            const element = dataViewer[index].viewers[i];
            if(element) {
              all = [...all, element];
            }
          }
          return all;
        })
        const dataCsv = [dataViewer.map(({createdAt}: any) => `${new Date(createdAt).toDateString()} ${new Date(createdAt).toLocaleTimeString()}`), ...arrayType]
        exportToCsv(`${streamer_user_name}_${new Date().getTime()}.csv`, dataCsv);
        console.log({getAllViewer: data?.getAllViewer, dataCsv});
      }
    },
  });

  useEffect(() => {
    if(Array.isArray(result?.data?.getAllCron)) {
      const is_have_job = result?.data?.getAllCron.find(({ streamer }: any) => streamer === streamer_user_name);
      console.log({is_have_job});
      if (is_have_job) {
        setJob(is_have_job)
      }
    }
  }, [result?.data]);

  const fetchStreamInformation = () => {
    const fetchLink = `${channelsUrl}?broadcaster_id=${streamer_id}`;

    fetch(fetchLink, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    })
      .then(function (response) {
        return response.json();
      })
      .then((data) => {
        setStreamer({
          streamID: data.data[0].broadcaster_id,
          streamerName: data.data[0].broadcaster_name,
          gameID: data.data[0].game_id,
          gameName: data.data[0].game_name,
          streamTitle: data.data[0].title,
        });
      });
  };

  const fetchStreamViewerCount = () => {
    fetch(`${streamsUrl}?user_id=${streamer_id}`, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    })
      .then(function (response) {
        return response.json();
      })
      .then((data) => {
        setStreamViewerCount(data?.data[0]?.viewer_count);
      });
  };

  const fetchFollowers = () => {
    fetch(`${followsUrl}?to_id=${streamer_id}`, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    })
      .then(function (response) {
        return response.json();
      })
      .then((data) => {
        setFollowers(data?.total);
      });
  };

  const insertTwitchEmbedScript = () => {
    const twitchEmbedDiv = document.querySelector("#twitch_embed_script_link");
    const twitch_embed_script = document.createElement("script");
    twitch_embed_script.setAttribute(
      "src",
      "https://embed.twitch.tv/embed/v1.js"
    );
    twitchEmbedDiv?.appendChild(twitch_embed_script);
  };

  const runTwitchEmbed = () => {
    const embed = new TwitchEmbed("twitch-embed", {
      width: "100%",
      height: 500,
      channel: streamer_user_name,
      layout: TwitchEmbedLayout.VIDEO_WITH_CHAT,
    });

    embed.addEventListener(TwitchPlayerEvent.READY, () => {
      const player = embed.getPlayer();
      player.play();
    });
  };

  const goToProfile = () => {
    history.push(`/profil-streamer/${streamer_user_name}/${streamer_id}`);
  };

  const goToClips = () => {
    history.push(`/captures/${streamer_user_name}/${streamer_id}`);
  };

  const fetchStreamViewerCountSetInterval = () => {
    setInterval(() => {
      fetchStreamViewerCount();
    }, 120000);
  };

  useEffect(() => {
    fetchStreamInformation();
    insertTwitchEmbedScript();
    fetchStreamViewerCount();
    fetchFollowers();
    setTimeout(() => runTwitchEmbed(), 500);
    // onGetViewer();
  }, []);

  useEffect(() => {
    fetchStreamViewerCountSetInterval();
  });

  const getVisibility = async () => {
    try {
      setLoading(true)
      // const { data } = await axios.get(`https://tmi.twitch.tv/group/user/${streamer_user_name.toLowerCase()}/chatters`, undefined);
      const { data } = await get(`public/chatters/${streamer_user_name.toLowerCase()}`, undefined);
      if (Array.isArray(data?.chatters?.viewers)) {
        const chatters = data?.chatters.viewers.map((viewer: any) => [viewer]);
        console.log(chatters);
        exportToCsv(`${streamer_user_name}_${new Date().getTime()}.csv`, chatters);
      }
      console.log({data});
    } catch (error) {
      console.log('===============>', error);
    } finally {
      setLoading(false)
    }
  }

  return {
    loading,
    streamer_user_name,
    job,
    item,
    followers,
    streamViewerCount,
    setItem,
    setJob,
    streamer,
    goToProfile,
    goToClips,
    getAllViewer,
    updateCron,
    addCron,
    getVisibility,
    loading_query,
    setLoadingQuery,
  }
}
