import { gql } from "@apollo/client";

export const VIEWER_QUERY = gql`
	query getAllViewer($skip: Int, $take: Int, $filter: String) {
		getAllViewer(skip: $skip, take: $take, filter: $filter) {
      id
      createdAt
      viewers
    }
	}
`;

export const CRON_QUERY = gql`
  query GetAllCron($skip: Int, $take: Int, $filter: Int) {
    getAllCron(skip: $skip, take: $take, filter: $filter) {
      id,
      streamer
      time
    }
  }
`;

export const CRON_UPDATE = gql`
  mutation updateCron($updateCronData: CronUpdateInput!) {
    updateCron(data: $updateCronData) {
      id
      status
      user
      time
    }
  }
`;

export const CRON_ADD = gql`
  mutation AddCron($addCronData: CronCreateInput!) {
    addCron(data: $addCronData) {
      id
      status
      user
      time
    }
  }
`;
