import {
  Card,
  CardActionArea,
  CardContent,
  CardMedia,
  Chip,
  Grid,
  makeStyles,
  Typography,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useIntersectionObserver } from "react-intersection-observer-hook";
import { useHistory } from "react-router-dom";
import FilterSection from "../../components/FilterSection/FilterSection";
import Loader from "../../components/Loader/Loader";
import Observer from "../../components/Observer/Observer";
import TopNavBarLinks from "../../components/TopNavBarLinks/TopNavBarLinks";
import {
  bearerToken,
  clientId,
  streamersApiUrl,
  streamsUrl,
} from "../../config/config";
import { dataCountList, dataStream } from "../../data/dataCountList";
import { CustomSelectProps } from "../../models/CustomSelectProps";
import { FilterSectionProps } from "../../models/FilterSectionProps";
import { checkIfAllToFetch, checkLanguage } from "../../utilities/Utility";
import VisibilityIcon from "@material-ui/icons/Visibility";
import SportsEsportsIcon from "@material-ui/icons/SportsEsports";
import "./GetTopStreamers.scss";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    height: "auto",
  },
  title: {
    margin: "1rem",
  },
  cardContent: {
    overflowY: "auto",
    height: "150px",
  },
  center: {
    display: "flex",
    justifyContent: "center",
  },
  viewersContainer: {
    display: "flex",
  },
  secondLine: {
    margin: "5px",
  },
}));

export class StreamerService {
	public streamers: Array<any> = [];
  cb: any;
  game_id?: string;

  constructor(cb: any, game_id?: string) {
    this.cb = cb;
    this.game_id = game_id;
    this.fetchAll(undefined);
  }

  fetchAll = async (tk: any) => {
    let defaultUrlApi = `${streamsUrl}?first=100`;
    if (this.game_id) {
      defaultUrlApi = `${streamsUrl}?first=100&game_id=${this.game_id}`;
    }
    let fetchLink = checkLanguage()
      ? `${defaultUrlApi}&language=${sessionStorage.getItem("language")}`
      : `${defaultUrlApi}`;

    if (tk) {
      fetchLink = checkLanguage()
        ? `${defaultUrlApi}&after=${tk}&language=${sessionStorage.getItem(
            "language"
          )}`
        : `${defaultUrlApi}`;
    }
    const res =  await fetch(fetchLink, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    });
    const jsonData = await res.json();
    this.streamers = [...this.streamers, ...jsonData?.data];
    if (jsonData?.pagination?.cursor) {
      await this.fetchAll(jsonData?.pagination?.cursor)
    } else {
      typeof this.cb === 'function' && this.cb(this.streamers);
    }
  }
}

const GetTopStreamers: React.FC = () => {
  const history = useHistory();
  const classes = useStyles();
  const [listOfTopStreamers, setlistOfTopStreamers] = useState<Array<any>>([]);
  const [pagination, setPagination] = useState<string>();
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [dataCount, setDataCount] = useState<any>(dataCountList[0]);
  const [stream, setStream] = useState<any>(dataStream[0]);
  const [ref, { entry }] = useIntersectionObserver();
  const isVisible = entry && entry.isIntersecting;
  const defaultUrlApi = `${streamsUrl}?first=${dataCount.value}`;

  const headers = [
    { label: "Id du stream", key: "id" },
    { label: "Titre du stream", key: "stream_title" },
    { label: "Id utilisateur", key: "user_id" },
    { label: "Pseudo", key: "user_name" },
    { label: "Id du jeu", key: "game_id" },
    { label: "Nombre de spectateurs", key: "viewer_count" },
    { label: "Url de l'aperu du stream", key: "thumbnail_url" },
  ];

  const csvReport = {
    data: listOfTopStreamers,
    headers: headers,
    filename: `${sessionStorage.getItem(
      "language"
    )} top streamers - ${new Date()}.csv`,
  };

  const customSelectProps: CustomSelectProps = {
    dataCount: dataCount,
    setDataCount: setDataCount,
    stream: stream,
    setStream: setStream,
  };

  const filterSectionProps: FilterSectionProps = {
    customSelectProps: customSelectProps,
    csvReport: csvReport,
    isLoading: isLoading,
  };

  const fetchTopStreamers = (pagination: any) => {
    let fetchLink = checkLanguage()
      ? `${defaultUrlApi}&language=${sessionStorage.getItem("language")}`
      : `${defaultUrlApi}`;

    if (pagination) {
      fetchLink = checkLanguage()
        ? `${defaultUrlApi}&after=${pagination}&language=${sessionStorage.getItem(
            "language"
          )}`
        : `${defaultUrlApi}&after=${pagination}`;
    }

    if (checkIfAllToFetch(dataCount)) {
      fetchLink = `${streamersApiUrl}/${sessionStorage.getItem(
        "language"
      )}/descendant`;
    }

    fetch(fetchLink, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    })
      .then(function (response) {
        return response.json();
      })
      .then((data) => {
        const dataListOfTopStreamers = [];
        if (!checkIfAllToFetch(dataCount)) {
          setPagination(data.pagination.cursor);
        }
        const list = checkIfAllToFetch(dataCount) ? data : data.data;
        for (const listKey in list) {
          dataListOfTopStreamers.push({
            id: list[listKey].id,
            user_id: list[listKey].user_id,
            user_name: list[listKey].user_name,
            game_id: list[listKey].game_id,
            stream_title: list[listKey].title,
            viewer_count: list[listKey].viewer_count,
            thumbnail_url: checkIfAllToFetch(dataCount)
              ? list[listKey].thumbnail_url
              : list[listKey].thumbnail_url
                ? list[listKey].thumbnail_url
                  .replace("{width}", "440")
                  .replace("{height}", "248")
                : '',
            game_name: list[listKey].game_name,
          });
        }
        const topStreamers = [...listOfTopStreamers, ...dataListOfTopStreamers];
        setlistOfTopStreamers(topStreamers);
      });
  };

  const intersected = () => {
    fetchTopStreamers(pagination);
  };

  useEffect(() => {
    if (listOfTopStreamers.length > 0) {
      setIsLoading(false);
    }
  });

  useEffect(() => {
    if (listOfTopStreamers.length === 0) {
      fetchTopStreamers(null);
    }
  }, []);

  useEffect(() => {
    if (isVisible) {
      intersected();
    }
  }, [isVisible]);

  useEffect(() => {
    setIsLoading(true);
    setlistOfTopStreamers([]);
    if (dataCount.value === 'all') {
      booclefetchStreamersUrl();
    } else {
      fetchTopStreamers(null);
    }
    // fetchStreamersToDisplay(null);
  }, [dataCount]);

  const booclefetchStreamersUrl = async () => {
    await setIsLoading(true);
    new StreamerService((list: any) => {
      let listOfStreams = [];
      for (const listKey in list) {
        const streamer = {
          id: list[listKey].id,
          user_id: list[listKey].user_id,
          user_name: list[listKey].user_name,
          game_id: list[listKey].game_id,
          stream_title: list[listKey].title,
          viewer_count: list[listKey].viewer_count,
          thumbnail_url: list[listKey].thumbnail_url
            ? list[listKey].thumbnail_url
              .replace("{width}", "440")
              .replace("{height}", "248")
            : '',
          game_name: list[listKey].game_name,
        };
        listOfStreams.push(streamer);
      }
      setlistOfTopStreamers(listOfStreams);
      setIsLoading(false);
    });
  }

  const goToStreamer = (topStreamer: any) => {
    history.push(`/streamer/${topStreamer.user_name}/${topStreamer.user_id}`);
  };

  const renderViewers = (topStreamer: any) => {
    return (
      <>
        <div className="viewers-container">
          <VisibilityIcon />
          <Typography>{topStreamer.viewer_count}</Typography>
        </div>
      </>
    );
  };

  const renderListOfStreams = () => {
    if (isLoading) {
      return <Loader />;
    }

    const data =  stream?.value === 'debutants'
      ? listOfTopStreamers.sort((a,b) => a.viewer_count - b.viewer_count)
      : listOfTopStreamers;

    return data.map((topStreamer: any, idx: number) => {
      return (
        <Grid
          item
          sm={4}
          md={3}
          key={idx}
          onClick={() => goToStreamer(topStreamer)}
        >
          <Card className={classes.root}>
            <CardActionArea>
              <CardMedia
                component="img"
                alt="Contemplative Reptile"
                height="300"
                image={`${topStreamer.thumbnail_url}`}
                title="Contemplative Reptile"
              />
              <CardContent className={classes.cardContent}>
                <Typography variant="h5" component="h5">
                  {topStreamer.user_name}
                </Typography>
                <Grid container className={classes.secondLine}>
                  <Grid item xs={6}>
                    <Typography variant="h6" component="h6">
                      <Chip label={renderViewers(topStreamer)} size="medium" />
                    </Typography>
                  </Grid>
                  <Grid item xs={6} className="game-container">
                    <SportsEsportsIcon />
                    <Typography> {topStreamer?.game_name} </Typography>
                  </Grid>
                </Grid>
                <Typography variant="body2" component="p">
                  {topStreamer.stream_title}
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        </Grid>
      );
    });
  };

  const renderObserver = () => {
    if (!checkIfAllToFetch(dataCount)) {
      return <Observer ref={ref}></Observer>;
    }
  };

  return (
    <>
      <TopNavBarLinks></TopNavBarLinks>
      <h2 className={classes.title}>{stream?.value === 'top' ? 'Top streamers' : 'Streamers dbutants'}</h2>
      <h3 className={classes.title}>
        {stream.value === 'top' ? 'Du plus grand au plus petit nombre de spectateurs' : 'Du plus petit au plus grand nombre de spectateurs'}
      </h3>
      <FilterSection {...filterSectionProps} />
      <div id="topStreamers">
        <Grid container item xs={12}>
          {renderListOfStreams()}
        </Grid>
      </div>
      {renderObserver()}
    </>
  );
};

export default GetTopStreamers;
