import {
  Chip,
  ImageList,
  GridListTile,
  GridListTileBar,
  IconButton,
  isWidthUp,
  makeStyles,
  Tooltip,
  Typography,
  withWidth,
} from "@material-ui/core";
import InfoIcon from "@material-ui/icons/Info";
import React, { useEffect, useState } from "react";
import { useIntersectionObserver } from "react-intersection-observer-hook";
import { useHistory, useParams } from "react-router-dom";
import FilterSection from "../../components/FilterSection/FilterSection";
import Loader from "../../components/Loader/Loader";
import Observer from "../../components/Observer/Observer";
import TopNavBarLinks from "../../components/TopNavBarLinks/TopNavBarLinks";
import {
  bearerToken,
  clientId,
  clipsUrl,
  serverClipsUrl,
} from "../../config/config";
import { dataCountList } from "../../data/dataCountList";
import { CustomSelectProps } from "../../models/CustomSelectProps";
import { FilterSectionProps } from "../../models/FilterSectionProps";
import { checkIfAllToFetch } from "../../utilities/Utility";
import VisibilityIcon from "@material-ui/icons/Visibility";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
  },
  gridList: {
    width: "auto",
    height: "auto",
  },
  title: {
    margin: "1rem",
  },
  icon: {
    color: "rgba(255, 255, 255, 0.54)",
  },
  gridListTile: {
    "&:hover": {
      cursor: "pointer",
    },
  },
  center: {
    display: "flex",
    justifyContent: "center",
  },
}));

const GetClips: React.FC<any> = (props) => {
  const classes = useStyles();
  const [listOfClips, setListOfClips] = useState<Array<any>>([]);
  const { streamer_name, streamer_id } = useParams<any>();
  const [pagination, setPagination] = useState<string>();
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [ref, { entry }] = useIntersectionObserver();
  const [dataCount, setDataCount] = useState<any>(dataCountList[0]);
  const defaultUrlApi = `${clipsUrl}?first=${dataCount.value}`;
  const isVisible = entry && entry.isIntersecting;
  const history = useHistory();

  const headers = [
    { label: "Id du clip", key: "id" },
    { label: "Titre", key: "title" },
    { label: "Id utilisateur", key: "user_id" },
    { label: "Pseudo", key: "user_name" },
    { label: "Nombre de spectateurs du clip", key: "view_count" },
    { label: "Url de l'aperu du clip", key: "thumbnail_url" },
    { label: "Langage", key: "language" },
  ];

  const csvReport = {
    data: listOfClips,
    headers: headers,
    filename: `Clips for ${streamer_name} - ${new Date()}.csv`,
  };

  const customSelectProps: CustomSelectProps = {
    dataCount: dataCount,
    setDataCount: setDataCount,
  };

  const filterSectionProps: FilterSectionProps = {
    customSelectProps: customSelectProps,
    csvReport: csvReport,
    isLoading: isLoading,
  };

  const fetchClips = (pagination: any) => {
    let fetchLink = `${defaultUrlApi}&broadcaster_id=${streamer_id}`;

    if (pagination) {
      fetchLink = `${fetchLink}&after=${pagination}`;
    }

    if (checkIfAllToFetch(dataCount)) {
      fetchLink = `${serverClipsUrl}/${streamer_id}`;
    }
    fetch(fetchLink, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    })
      .then(function (response) {
        return response.json();
      })
      .then((data) => {
        const dataListOfClips = [];
        if (!checkIfAllToFetch(dataCount)) {
          setPagination(data.pagination.cursor);
        }
        const list = checkIfAllToFetch(dataCount) ? data : data.data;
        for (const listKey in list) {
          dataListOfClips.push({
            id: list[listKey].id,
            user_id: list[listKey].broadcaster_id,
            user_name: list[listKey].broadcaster_name,
            language: list[listKey].language,
            title: list[listKey].title,
            thumbnail_url: checkIfAllToFetch(dataCount)
              ? list[listKey].thumbnail_url
              : list[listKey].thumbnail_url
                  .replace("{width}", "440")
                  .replace("{height}", "248"),
            view_count: list[listKey].view_count,
            embed_url: list[listKey].embed_url,
            url: list[listKey].url,
            creator_name: list[listKey].creator_name,
          });
        }

        const result = [...listOfClips, ...dataListOfClips];
        setListOfClips(result);
      });
  };

  const creatorName = (clip: any) => {
    return <>Crateur de la capture : {clip.creator_name}</>;
  };

  const renderViewers = (clip: any) => {
    return (
      <>
        <div className="viewers-container">
          <VisibilityIcon />
          <Typography>{clip.view_count}</Typography>
        </div>
      </>
    );
  };

  const renderClipViewers = (clip: any) => {
    return (
      <>
        <Chip label={renderViewers(clip)} size="medium" />
      </>
    );
  };

  const renderTopStreamersForSpecificGame = () => {
    if (isLoading) {
      return <Loader />;
    }
    return (
      <ImageList
        cellHeight={"auto"}
        cols={getGridListCols()}
        className={classes.gridList}
      >
        {listOfClips.map((clip: any, idx: number) => {
          return (
            <GridListTile
              onClick={() => gotToClip(clip.id)}
              key={idx}
              className={classes.gridListTile}
            >
              <img src={`${clip.thumbnail_url}`} alt={clip.user_name} />
              <GridListTileBar
                title={`${clip.title}`}
                subtitle={renderClipViewers(clip)}
                actionIcon={
                  <Tooltip title={creatorName(clip)} placement="top">
                    <IconButton className={classes.icon}>
                      <InfoIcon />
                    </IconButton>
                  </Tooltip>
                }
              />
            </GridListTile>
          );
        })}
      </ImageList>
    );
  };

  const gotToClip = (clip_id: string) => {
    history.push(`/capture/${clip_id}`);
  };

  const intersected = () => {
    fetchClips(pagination);
  };

  useEffect(() => {
    if (listOfClips.length === 0) {
      fetchClips(null);
    }
    if (listOfClips.length > 0) {
      setIsLoading(false);
    }
  }, [listOfClips.length, fetchClips]);

  useEffect(() => {
    intersected();
  }, [isVisible]);

  useEffect(() => {
    setListOfClips([]);
    fetchClips(null);
    setIsLoading(true);
  }, [dataCount]);

  const getGridListCols = () => {
    if (isWidthUp("xl", props.width)) {
      return 6;
    }

    if (isWidthUp("lg", props.width)) {
      return 5;
    }

    if (isWidthUp("md", props.width)) {
      return 4;
    }

    if (isWidthUp("sm", props.width)) {
      return 2;
    }

    if (isWidthUp("xs", props.width)) {
      return 1;
    }

    return 1;
  };

  const renderObserver = () => {
    if (!checkIfAllToFetch(dataCount)) {
      return <Observer ref={ref}></Observer>;
    }
  };

  return (
    <div id="streamersForGameContainer">
      <TopNavBarLinks></TopNavBarLinks>
      <h2 className={classes.title}>Captures pour '{streamer_name}'</h2>
      <FilterSection {...filterSectionProps} />
      <div className={classes.root}>{renderTopStreamersForSpecificGame()}</div>
      {renderObserver()}
    </div>
  );
};

export default withWidth()(GetClips);
