import {
  ImageListItem,
  IconButton,
  ImageList,
  isWidthUp,
  makeStyles,
  Tooltip,
  withWidth,
} from "@material-ui/core";
import ImageListItemBar from "@material-ui/core/ImageListItemBar";
import InfoIcon from "@material-ui/icons/Info";
import React, { useEffect, useState } from "react";
import { useIntersectionObserver } from "react-intersection-observer-hook";
import { useHistory } from "react-router-dom";
import FilterSection from "../../components/FilterSection/FilterSection";
import Loader from "../../components/Loader/Loader";
import Observer from "../../components/Observer/Observer";
import TopNavBarLinks from "../../components/TopNavBarLinks/TopNavBarLinks";
import {
  bearerToken,
  clientId,
  gamesUrl,
  topGamesUrl,
} from "../../config/config";
import { dataCountList } from "../../data/dataCountList";
import { CustomSelectProps } from "../../models/CustomSelectProps";
import { FilterSectionProps } from "../../models/FilterSectionProps";
import { HashMap } from "../../models/HashMap";
import { checkIfAllToFetch, checkLanguage } from "../../utilities/Utility";
import "./GetTopGames.scss";


export interface Game {
  gameID: any;
  gameName: any;
  gameBoxArtURL: any;
}

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
  },
  gridList: {
    width: "auto",
    height: "auto",
  },
  title: {
    margin: "1rem",
  },
  icon: {
    color: "rgba(255, 255, 255, 0.54)",
  },
  ImageListItem: {
    "&:hover": {
      cursor: "pointer",
    },
  },
  imageTile: {
    width: "100%",
  },
  titleGroup: {
    display: "flex",
  },
}));
const defaultUrlApi = `${topGamesUrl}?first=100`;
class StreamerService {
	public streamers: Array<any> = [];
  cb: any;

  constructor(cb: any) {
    this.cb = cb;
    this.fetchAll(undefined);
  }

  fetchAll = async (tk: any) => {
    let fetchLink = checkLanguage()
      ? `${defaultUrlApi}&language=${sessionStorage.getItem("language")}`
      : `${defaultUrlApi}`;

    if (tk) {
      fetchLink = checkLanguage()
        ? `${defaultUrlApi}&after=${tk}&language=${sessionStorage.getItem(
            "language"
          )}`
        : `${defaultUrlApi}&after=${tk}`;
        console.log(fetchLink);
    }
    const res =  await fetch(fetchLink, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    });
    const jsonData = await res.json();
    this.streamers = [...this.streamers, ...jsonData?.data];
    if (jsonData?.pagination?.cursor) {
      await this.fetchAll(jsonData?.pagination?.cursor)
    } else {
      typeof this.cb === 'function' && this.cb(this.streamers);
    }
  }
}

const GetTopGames: React.FC<any> = (props) => {
  const history = useHistory();
  const classes = useStyles();
  const [ref, { entry }] = useIntersectionObserver();
  const isVisible = entry && entry.isIntersecting;
  const [pagination, setPagination] = useState<string>();
  const [listOfTopGames, setListOfTopGames] = useState<Array<Game>>([]);
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [dataCount, setDataCount] = useState<HashMap>(dataCountList[0]);
  const [nb, setNb] = useState<number>(0);
  const defaultUrlApi = `${topGamesUrl}?first=${dataCount.value}`;

  const headers = [
    { label: "Id du jeu", key: "gameID" },
    { label: "Nom du jeu", key: "gameName" },
    { label: "Url de l'image du jeu", key: "gameBoxArtURL" },
  ];

  const csvReport = {
    data: listOfTopGames,
    headers: headers,
    filename: `Top Games - ${new Date()}.csv`,
  };

  const customSelectProps: CustomSelectProps = {
    dataCount: dataCount,
    setDataCount: setDataCount,
  };

  const filterSectionProps: FilterSectionProps = {
    customSelectProps: customSelectProps,
    csvReport: csvReport,
    isLoading: isLoading,
  };

  const fetchTopGames = (pagination: any) => {
    // To get correct Authorization Bearer OAuth code read forum post here - https://discuss.dev.twitch.tv/t/how-do-i-get-correct-oauth-token/27986
    let fetchLink = checkLanguage()
      ? `${defaultUrlApi}&language=${sessionStorage.getItem("language")}`
      : defaultUrlApi;

    if (pagination) {
      fetchLink = checkLanguage()
        ? `${defaultUrlApi}&after=${pagination}&language=${sessionStorage.getItem(
            "language"
          )}`
        : `${defaultUrlApi}&after=${pagination}`;
    }

    if (checkIfAllToFetch(dataCount)) {
      fetchLink = gamesUrl;
    }

    fetch(fetchLink, {
      method: "get",
      headers: new Headers({
        Authorization: `Bearer ${bearerToken}`,
        "Client-ID": clientId,
      }),
    })
      .then(function (response) {
        return response.json();
      })
      .then(
        // need to use arrow function here for vue.js to accept 'this' statement
        (data) => {
          // this array will be combined with the array in the listOfTopGames array above
          const dataListOfTopGames = [];
          if (!checkIfAllToFetch(dataCount)) {
            setPagination(data.pagination.cursor);
          }
          const list = checkIfAllToFetch(dataCount) ? data : data.data;
          for (const listKey in list) {
            dataListOfTopGames.push({
              gameID: list[listKey].id,
              gameName: list[listKey].name,
              gameBoxArtURL: checkIfAllToFetch(dataCount)
                ? list[listKey].box_art_url
                : list[listKey].box_art_url
                    .replace("{width}", "213")
                    .replace("{height}", "285"),
            });
          }
          const topGames = [...listOfTopGames, ...dataListOfTopGames];
          setIsLoading(false);
          setListOfTopGames(topGames);
          setNb(topGames.length)
        }
      );
  };

  const intersected = () => {
    fetchTopGames(pagination);
  };

  useEffect(() => {
    if (listOfTopGames.length === 0) {
      fetchTopGames(null);
    }
    var lastScroll = 0;

    window.onscroll = function() {
      let currentScroll = document.documentElement.scrollTop || document.body.scrollTop; // Get Current Scroll Value
      if (currentScroll > 0 && lastScroll <= currentScroll) {
        lastScroll = currentScroll;
        console.log('lastScroll  ====', listOfTopGames);
        setNb((nb + 100) < listOfTopGames.length ? (nb + 100) : listOfTopGames.length - 1);
        console.log((nb + 100) < listOfTopGames.length ? (nb + 100) : listOfTopGames.length - 1)
      }
    };
  }, []);

  useEffect(() => {
    if (isVisible) {
      intersected();
    }
  }, [isVisible]);

  useEffect(() => {
    setIsLoading(true);
    setListOfTopGames([]);
    if (dataCount.value === 'all') {
      booclefetchStreamersUrl();
    } else {
      fetchTopGames(null);
    }
    // fetchStreamersToDisplay(null);
  }, [dataCount]);

  const booclefetchStreamersUrl = async () => {
    await setIsLoading(true);
    new StreamerService((list: any) => {
      let listOfStreams: React.SetStateAction<Game[]> = [];
      let i = 0;
      for (const listKey in list) {
        const streamer = {
          gameID: list[listKey].id,
              gameName: list[listKey].name,
              gameBoxArtURL: list[listKey].box_art_url
                ? list[listKey].box_art_url
                .replace("{width}", "213")
                .replace("{height}", "285")
                : '',
        };
        listOfStreams.push(streamer);
        if (listOfStreams.length % 100 === 0) {
          setTimeout(() => {
            console.log({ listOfStreams });
            setListOfTopGames(listOfStreams);
            setIsLoading(false);
          }, 3000)
        }
      }
      console.log({ listOfStreams });
      setListOfTopGames(listOfStreams);
      setIsLoading(false);
      // do {
        
      // } while ((nb + 100) < listOfStreams.length)
    });
  }

  const goToStreamers = (topGame: any) => {
    history.push(`/streamers-par-jeu/${topGame.gameName}/${topGame.gameID}`);
  };

  const renderListOfGames = () => {
    if (isLoading) {
      return <Loader />;
    }
    return (
      <div className={classes.root}>
        <ImageList
          rowHeight={"auto"}
          cols={getGridListCols()}
          className={classes.gridList}
        >
          {listOfTopGames.slice(0, nb).map((topGame: any, idx: number) => {
            return (
              <ImageListItem
                onClick={() => goToStreamers(topGame)}
                key={idx}
                className={classes.ImageListItem}
              >
                <img
                  src={topGame.gameBoxArtURL}
                  alt={topGame.gameName}
                  className={classes.imageTile}
                />
                <ImageListItemBar
                  title={topGame.gameName}
                  actionIcon={
                    <Tooltip title={topGame.gameName} placement="top">
                      <IconButton className={classes.icon}>
                        <InfoIcon />
                      </IconButton>
                    </Tooltip>
                  }
                />
              </ImageListItem>
            );
          })}
        </ImageList>
      </div>
    );
  };

  // useEffect(() => {
  //   setListOfTopGames([]);
  //   fetchTopGames(null);
  //   setIsLoading(true);
  // }, [dataCount]);

  const getGridListCols = () => {
    if (isWidthUp("xl", props.width)) {
      return 6;
    }

    if (isWidthUp("lg", props.width)) {
      return 5;
    }

    if (isWidthUp("md", props.width)) {
      return 4;
    }

    if (isWidthUp("sm", props.width)) {
      return 2;
    }

    if (isWidthUp("xs", props.width)) {
      return 1;
    }

    return 1;
  };

  const renderObserver = () => {
    if (!checkIfAllToFetch(dataCount)) {
      return <Observer ref={ref}></Observer>;
    }
  };

  return (
    <div>
      <TopNavBarLinks></TopNavBarLinks>
      <div>
        <h2 className={classes.title}>Top jeux</h2>
        <FilterSection {...filterSectionProps}></FilterSection>
      </div>
      <div id="topGames">{renderListOfGames()}</div>
      {renderObserver()}
    </div>
  );
};

export default withWidth()(GetTopGames);
